.card {
    width: 30%;
    margin-bottom: 50px;
    background: #f5f5f5;
    overflow: hidden;
    position: relative;
    padding-bottom: 20px;
}

.card img {
    width: 100%;
    border-radius: 6px 6px 0 0;
    transition: 0.6s;
    cursor: pointer;
}

.card img:hover {
    transform: scale(1.05);
}

.card__title , 
.card__desc,
.card__price {
    margin: 10px 20px;
    color: #333;
}

.card__add {
    position: absolute;
    right: 20px;
    bottom: 20px;
    background: rgb(49, 164, 3);
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    color: #fff;
    border-radius: 50%;
    cursor: pointer;
    transition: 0.6s;
}

.card__add:hover {
    transform: scale(1.05) translateY(-5px);
}