.categories {
    margin-bottom: 25px;
}

.categories div {
    cursor: pointer;
    display: inline;
    background: #e7e7e7;
    border-radius: 10px;
    padding: 10px 20px;
    margin-right: 15px;
    border: 1px solid transparent;
    transition: 0.3s;
}

.categories div:hover {
    border-color: silver;
    
}